$ms: .3s;

.menuContainer {
  height: 100%;
  width: 100%;
  z-index: 1000;

  .menu {
    width: 250px;
    transition: $ms;
    position: fixed;
    top: 0;
    left: -250px;
    background: var(--main-bg-color-light);
    height: 100%;
  }

  .background {
    width: 100%;
    height: 100%;
    transition: opacity $ms;
    position: fixed;
    top: 0;
    left: -100%;
    background: black;
    opacity: 0;
  }

  &_visible {
    .menu {
      left: 0;
    }
    .background {
      left: 0;
      opacity: .5;
    }
  }

  &_hidden {
    .menu {
      left: -250px;
    }
    .background {
      opacity: 0;
      left: 0;
    }
  }

  &_hiddenDone {
    .background {
      left: -100%;
    }
  }
}


@media (prefers-color-scheme: dark) {
  .menuContainer {
    .menu{
      background-color: var(--main-bg-color-dark);
      color: var(--main-color-dark);
    }
  }
}